const STATUS_ROOM = 0
const STATUS_RUNNING = 1
const STATUS_FINAL_VOTATION = 2
const STATUS_COMPUTING = 3
const STATUS_PODIUM_SPY = 4
const STATUS_INTERRUPTION = 5
const STATUS_PODIUM_TEAM = 7
const STATUS_PODIUM_TEAM_SPY_FALL = 8
export default {
  winner: {
    team: 1,
    spy: 2
  },
  numberOf: {
    spy: 1,
    roundCard: 1,
    cards: 16
  },
  points: {
    spy: 1,
    spyVote: 2,
    team: 1,
    teamVote: 2
  },
  objects: {
    spyCard: {
      title: 'Você é o infiltrado',
      image: 'infiltrado.jpg',
      role: 'Tente se disfarçar'
    }
  },
  check: {
    isRunnig: (status) => {
      return status === STATUS_RUNNING
    },
    isPlayable: (status) => {
      return status === STATUS_RUNNING || status === STATUS_INTERRUPTION
    },
    isScoreable: (status) => {
      return status === STATUS_FINAL_VOTATION || status === STATUS_RUNNING
    },
    isComputable: (status) => {
      return (
        status === STATUS_RUNNING ||
        status === STATUS_FINAL_VOTATION ||
        status === STATUS_INTERRUPTION
      )
    },
    isPodium: (status) => {
      return (
        status === STATUS_PODIUM_SPY ||
        status === STATUS_PODIUM_TEAM ||
        status === STATUS_PODIUM_TEAM_SPY_FALL
      )
    }
  },
  status: {
    room: STATUS_ROOM,
    running: STATUS_RUNNING,
    finalVotation: STATUS_FINAL_VOTATION,

    computing: STATUS_COMPUTING,
    podiumSpy: STATUS_PODIUM_SPY,
    podiumTeam: STATUS_PODIUM_TEAM,
    podiumTeamSpyFall: STATUS_PODIUM_TEAM_SPY_FALL,
    interruption: STATUS_INTERRUPTION
  },
  times: {
    finalVotation: 2,
    interruption: 1
  }
}
