<template>
  <div
    ref="modal"
    class="c-modal"
    :class="styles"
    :aria-label="title"
    role="dialog"
  >
    <div class="c-modal__overlay" @click="close" />
    <div class="c-modal__dialog">
      <div class="c-modal__header">
        <Btn
          v-if="closeable"
          class="c-modal__close-btn v--rounded"
          @click="close"
        >
          <icon icon-name="x" />
        </Btn>
        <div v-if="hasHeader">
          <h1 class="c-modal__title">
            {{ title }}
          </h1>
          <p v-if="subtitle" class="c-modal__subtitle">
            {{ subtitle }}
          </p>
        </div>
        <slot name="header" />
      </div>
      <div v-if="$slots.default" class="c-modal__body">
        <slot />
      </div>
      <div v-if="$slots.footer" class="c-modal__footer">
        <slot name="footer" />
      </div>
      <div v-if="controllers" class="c-modal__controllers">
        <div>
          <slot name="left-controllers" />
        </div>
        <div class="v--flex-align-right">
          <Btn
            v-if="negativeButtonLabel"
            class="v--light v--rounded v--horizontal-list-item"
            @click="close"
          >
            {{ negativeButtonLabel }}
          </Btn>
          <Btn
            class="v--info v--rounded v--horizontal-list-item"
            :disabled="isPositiveButtonDisabled"
            :loading="positiveButtonIsLoading"
            @click="save"
          >
            {{ positiveButtonLabel }}
          </Btn>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
let firstFocusableElement
let lastFocusableElement
const firstFocusableElementRule = (e) => {
  if (e.shiftKey && e.key === 'Tab') {
    e.preventDefault()
    lastFocusableElement.focus()
  }
}

const lastFocusableElementRule = (e) => {
  if (!e.shiftKey && e.key === 'Tab') {
    e.preventDefault()
    firstFocusableElement.focus()
  }
}
export default {
  name: 'Modal',
  props: {
    title: {
      default: '',
      type: String
    },
    subtitle: {
      default: '',
      type: String
    },
    negativeButtonLabel: {
      default: 'Cancelar',
      type: String
    },
    positiveButtonLabel: {
      default: 'Salvar',
      type: String
    },
    positiveButtonIsLoading: {
      default: false,
      type: Boolean
    },
    positiveButtonIsDisabled: {
      default: false,
      type: Boolean
    },
    closeable: {
      default: true,
      type: Boolean
    },
    controllers: {
      default: true,
      type: Boolean
    }
  },
  emits: ['save', 'close'],
  data() {
    return {}
  },
  computed: {
    styles() {
      return {
        ['v--without-controllers']: !this.controllers
      }
    },
    isPositiveButtonDisabled() {
      return this.positiveButtonIsLoading || this.positiveButtonIsDisabled
    },
    hasHeader() {
      return !!(this.title || this.subtitle || this.$slots.header)
    }
  },
  mounted() {
    document.querySelector('body').setAttribute('style', 'overflow:hidden;')
    document.documentElement.setAttribute('style', 'overflow:hidden;')
    document.addEventListener('keydown', this.handleKeyboardKeys)

    let focusable = []

    if (this.$refs.modal) {
      focusable = [].slice.call(
        this.$refs.modal.querySelectorAll(
          'button, [href], input, select, textarea, [tabindex]'
        )
      )
    }
    if (focusable.length) {
      firstFocusableElement = focusable[0]

      firstFocusableElement.addEventListener(
        'keydown',
        firstFocusableElementRule
      )

      lastFocusableElement = focusable[focusable.length - 1]
      lastFocusableElement.addEventListener('keydown', lastFocusableElementRule)
    }
  },

  destroyed() {
    document.querySelector('body').setAttribute('style', '')
    document.documentElement.removeAttribute('style')
    document.removeEventListener('keydown', this.handleKeyboardKeys)
    if (firstFocusableElement) {
      firstFocusableElement.removeEventListener(
        'keydown',
        firstFocusableElementRule
      )
    }
    if (lastFocusableElement) {
      lastFocusableElement.removeEventListener(
        'keydown',
        lastFocusableElementRule
      )
    }
  },
  methods: {
    handleKeyboardKeys(event) {
      const { key } = event
      if (key === 'Escape') {
        this.close()
      }
    },
    close() {
      if (this.closeable) {
        this.$emit('close')
      }
    },
    save() {
      this.$emit('save')
    }
  }
}
</script>

<style src="./styles.styl" lang="stylus" scoped />
