<template>
  <div
    class="c-card"
    :class="{ 'c-card--hoverable': clickable, 'c-card--shadow': discarded }"
    @click="toggleDiscarded"
  >
    <div
      v-if="discarded"
      class="c-card__discarded"
      :style="[{ 'background-image': `url(${getImgUrl('big-x.png')})` }]"
    />
    <div
      class="c-card__content"
      :style="[{ 'background-image': `url(${getImgUrl(image)})` }]"
    >
      <div class="c-card__header">
        <h2>{{ title }}</h2>
        <p>{{ subtitle }}</p>
      </div>
    </div>
    <Btn
      v-if="showButtons"
      @click.native.stop="$emit('vote', title)"
      class="c-card__btn v--dark"
    >
      <icon icon-name="thumbs-up" />
    </Btn>
  </div>
</template>

<script>
export default {
  name: 'Card',
  props: {
    title: {
      type: String,
      default: ''
    },
    subtitle: {
      type: String,
      default: ''
    },
    image: {
      type: String,
      default: ''
    },
    showButtons: {
      type: Boolean,
      default: false
    },
    clickable: {
      type: Boolean,
      default: true
    }
  },
  data() {
    return {
      discarded: false
    }
  },
  methods: {
    getImgUrl(image) {
      var images = require.context(
        '@/assets/images/cards',
        false,
        /\.(png|jpg)$/
      )
      return images('./' + image)
    },
    toggleDiscarded() {
      if (this.clickable) {
        this.discarded = !this.discarded
      }
    }
  }
}
</script>

