<template>
  <div>
    <div class="c-field_container">
      <div v-if="label" class="c-field_label">
        <label :for="name">
          {{ label }}
        </label>
      </div>
      <div class="c-field_box">
        <textarea
          :id="name"
          :value="value"
          :name="name"
          :placeholder="placeholder"
          :class="styles"
          :autofocus="autofocus"
          :disabled="disabled"
          class="c-field"
          :rows="rows"
          @input="$emit('input', $event.target.value)"
          @blur="blur"
          @focus="$emit('focus')"
          @keydown.enter="enterPress"
        />
      </div>
    </div>
    <span v-if="showErrorMessage" class="c-field_message">
      {{ errorMessage }}
    </span>
  </div>
</template>

<script>
export default {
  name: 'TextField',
  props: {
    name: {
      type: String,
      default: 'name',
      required: true
    },
    label: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    hasError: {
      type: Boolean,
      default: false
    },
    errorMessage: {
      type: String,
      default: 'Faltou preencher aqui.'
    },
    helperMessage: {
      type: String,
      default: ''
    },
    disabled: {
      type: Boolean,
      default: false
    },
    autofocus: {
      type: Boolean,
      default: false
    },
    rounded: {
      type: Boolean,
      default: false
    },
    submitWithEnter: {
      type: Boolean,
      default: false
    },
    value: {
      type: String,
      default: ''
    },
    rows: {
      type: [String, Number],
      default: '4'
    }
  },
  data() {
    return {
      isErrorChecked: null
    }
  },
  computed: {
    styles() {
      return {
        'v--has-error': this.showErrorMessage,
        'v--rounded': this.rounded && this.rows === 1
      }
    },
    showErrorMessage() {
      return (
        (this.hasError && this.isErrorChecked === null) ||
        (this.isErrorChecked && this.hasError)
      )
    }
  },
  methods: {
    enterPress(e) {
      if (!e.shiftKey && this.submitWithEnter) {
        e.preventDefault()
        this.$emit('enterpress', e)
      }
    },
    blur() {
      if (this.hasError) {
        this.isErrorChecked = true
      } else {
        this.isErrorChecked = false
      }
      this.$emit('blur')
    }
  }
}
</script>
<style lang="stylus" src="@/components/global/field/style.styl" scoped></style>
<style lang="stylus" scoped>
.v--rounded {
  border-radius: 20.5px;
}
</style>
