<template>
  <div class="c-copyArea">
    <div>
      {{ url }}
    </div>
    <Btn
      class="c-copyArea__button"
      v-clipboard="url"
      v-clipboard:success="copied"
      v-clipboard:error="error"
    >
      {{ btnTitle }}
    </Btn>
  </div>
</template>
<script>
export default {
  name: 'CopyArea',
  props: {
    value: {
      type: String,
      default: ''
    },
    btnTitle: {
      type: String,
      default: 'Copiar'
    }
  },
  computed: {
    url() {
      return process.env.VUE_APP_SITE_URL + 'room/' + this.value
    }
  },
  methods: {
    copied(event) {
      this.$toast.success('Link copiado!')
      this.$emit('copied', event)
    },
    error(event) {
      this.$emit('error', event)
    }
  }
}
</script>
