<template>
  <div class="c-scoreBoard">
    <h2 v-if="!compacted && showTitle" class="c-scoreBoard__title">
      Jogadores
    </h2>
    <template v-for="user in items">
      <div
        v-if="(user && user.id !== userId) || !compacted"
        class="c-scoreBoard__item"
        :class="{
          'v--info': user && user.id === userId,
          'v--grey': !inRound(user.round)
        }"
        :key="user.id"
      >
        <h4 v-if="!compacted" class="c-scoreBoard__score">
          {{ score(user.score) }}
        </h4>
        <div
          v-if="!isYou(user.id) && inRound(user.round)"
          class="c-scoreBoard__person"
        >
          <h3 class="c-scoreBoard__name">{{ user.name }}</h3>
          <Btn
            v-if="showButtons"
            @click="vote(user)"
            class="c-scoreBoard__vote v--small"
            :class="{ 'c-scoreBoard__vote--visible': compacted }"
          >
            Votar
          </Btn>
        </div>
        <div v-else class="c-scoreBoard__person">
          <h3 class="c-scoreBoard__name">
            {{ isYou(user.id) ? 'Tu: ' : '' }} {{ user.name }}
          </h3>
        </div>
      </div>
    </template>
    <template v-if="$slots.footer">
      <div class="c-scoreBoard__line" />
      <slot name="footer"> </slot>
    </template>
    <portal to="modal">
      <OpenVotationModal
        v-if="showForm"
        :target="showForm"
        :is-spy="isSpy"
        @close="showForm = null"
        @cancel-votation="showForm = null"
        @open-votation="openVotation"
      />
    </portal>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import OpenVotationModal from '@/components/modals/OpenVotationModal'
export default {
  name: 'ScoreBoard',
  props: {
    isSpy: {
      type: Boolean,
      default: false
    },
    showButtons: {
      type: Boolean,
      default: false
    },
    compacted: {
      type: Boolean,
      default: false
    },
    users: {
      type: Array,
      default: () => []
    },
    canVote: {
      type: Boolean,
      default: true
    },
    showTitle: {
      type: Boolean,
      default: true
    },
    round: {
      type: Object,
      defautl: () => ({})
    }
  },
  components: {
    OpenVotationModal
  },
  data() {
    return {
      showForm: false
    }
  },
  computed: {
    ...mapState({
      userId: (state) => state.user.id
    }),
    items() {
      return this.users
        .filter((item) => {
          return item
        })
        .sort((a, b) => {
          const aScore = this.score(a.score)
          const bScore = this.score(b.score)
          if (aScore > bScore) {
            return -1
          }
          return aScore < bScore
        })
    }
  },
  methods: {
    inRound(userRound) {
      return this.round.num && userRound && userRound.num === this.round.num
    },
    isYou(id) {
      return id === this.userId
    },
    vote(user) {
      if (this.canVote) {
        if (!this.compacted) {
          this.showForm = user
        } else {
          this.$emit('vote', user)
        }
      } else {
        this.$toast.info('Você já abriu votação neste round!')
      }
    },
    openVotation() {
      this.$emit('open-votation', this.showForm)
      this.showForm = null
    },
    score(data) {
      let sum = 0
      if (data) {
        Object.keys(data).forEach((key) => {
          sum += data[key]
        })
      }
      return sum
    }
  }
}
</script>
