<template>
  <Modal
    title="Fim de papo!"
    :subtitle="subtitle"
    :closeable="false"
    :controllers="false"
  >
    <Countdown
      class="c-endGameModal__counter"
      :round-time="times.endGame"
      :final-time="endTime"
      @timeout="$emit('timeout')"
    />
    <template v-if="!isSpy">
      <ScoreBoard
        v-if="!computedVote"
        :users="users"
        compacted
        show-buttons
        :round="room.round"
        @vote="$emit('vote', $event)"
      />
      <h3 v-else>
        {{ waitingText }}
      </h3>
    </template>

    <template v-else>
      <h3>Participe da discussão, e finja votar em alguém.</h3>
      <h4>Seu papel agora é se livrar das suspeitas</h4>
    </template>
    <template #footer><div /> </template>
  </Modal>
</template>
<script>
import times from '@/defaults/time'
import ScoreBoard from '@/components/ScoreBoard'
export default {
  name: 'EndGame',
  components: {
    ScoreBoard
  },
  props: {
    isSpy: {
      type: Boolean,
      default: true
    },
    computedVote: {
      type: Boolean,
      default: true
    },
    endTime: {
      type: [String, Number],
      default: 0
    },
    users: {
      type: Array,
      default: () => []
    },
    room: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      times
    }
  },
  computed: {
    subtitle() {
      if (this.isSpy) {
        return 'Tente se defender! Sua identidade está em risco!'
      }
      return this.computedVote
        ? 'Seu voto foi computado!'
        : 'Marque quem você acha que é o infiltrado da rodada'
    },
    waitingText() {
      return this.computedVote
        ? 'Aguardando colegas...'
        : 'Calculando pontuação...'
    }
  }
}
</script>
<style lang="stylus" scoped>
.c-endGameModal__counter
  display flex
  justify-content center
  margin 0 auto
  & >>> h2
    font-size 35px!important
</style>
