<template>
  <div class="c-soundBtn">
    <Btn
      @click="toggleSound"
      v-tooltip="this.sound ? 'Mutar som' : 'Ativar som'"
      class="c-scoreBoard__vote"
      ><icon :icon-name="iconName"
    /></Btn>
  </div>
</template>

<script>
import { mapState } from 'vuex'
export default {
  name: 'SoundBtn',
  computed: {
    ...mapState({
      sound: (state) => state.config.sound
    }),
    iconName() {
      return this.sound ? 'volume' : 'volume-x'
    }
  },
  methods: {
    toggleSound() {
      this.$store.dispatch('config/toggleSound')
    }
  }
}
</script>

