<template>
  <Modal
    :header="false"
    :closeable="false"
    :controllers="false"
    class="c-votationModal"
  >
    <Countdown
      class="c-votationModal__counter"
      :round-time="gameConfig.times.interruption"
      :final-time="votationEnd"
      :end-round="votationEnd"
      @timeout="endRound"
    />
    <Countdown class="c-endGameModal__counter" />
    <h2 v-if="status === 1 || isOwner">
      Vamos aguardar a decisão dos colegas...
    </h2>
    <template v-else>
      <h2 v-if="userCanVote">
        <b>{{ by().name }}</b> abriu uma votação contra
        <b class="c-votationModal__choosed">{{ target().name }}</b
        >. Você concorda?
      </h2>
      <h2 v-else v-html="spyText" />
    </template>
    <template #footer>
      <div v-if="userCanVote && !isOwner" class="c-votationModal__buttons">
        <Btn class="v--block v--dark" @click="yes">
          Sim
        </Btn>
        <Btn class="v--block v--dark" @click="$emit('no-votation')">
          Não
        </Btn>
      </div>
      <div v-else></div>
    </template>
  </Modal>
</template>
<script>
import { mapState } from 'vuex'
import gameConfig from '@/defaults/game-config'
export default {
  name: 'InterruptionModal',
  props: {
    users: {
      type: Array,
      default: () => []
    },
    round: {
      type: Object,
      default: () => ({})
    },
    isSpy: {
      type: Boolean,
      default: true
    },
    votationEnd: {
      type: [String, Number],
      default: 0
    }
  },
  data() {
    return { status: 0, gameConfig }
  },
  computed: {
    ...mapState({
      userId: (state) => state.user.id
    }),
    spyText() {
      const by = this.by()
      const target = this.target()
      if (target.id === this.userId) {
        return `Eita! ${by.name} abriu uma votação contra você! Se defenda! Rápido!`
      } else {
        return `Opa! ${
          by.name
        } abriu uma votação contra <span style="color: #F27A3C">${
          this.target().name
        }</span>. Fique atento aos comentários, e tente se livrar de qualquer suspeita!`
      }
    },
    isOwner() {
      return this.round.votationOwner === this.userId
    },
    userCanVote() {
      // Ainda não votou
      // Não é o alvo da votação
      return !this.status && !(this.target().id === this.userId)
    }
  },
  methods: {
    by() {
      return (
        this.users.find((user) => {
          return user.id === this.round.votationOwner
        }) || {}
      )
    },
    target() {
      if (this.round && this.round.votationOwner && this.round.votations) {
        return (
          this.users.find((user) => {
            return (
              user.id === this.round.votations[this.round.votationOwner].target
            )
          }) || {}
        )
      }
      return {}
    },
    yes() {
      this.status = 1
      this.$emit('yes-votation')
    },
    endRound() {
      this.$emit('timeout')
    }
  }
}
</script>
<style lang="stylus" scoped>
@import '../../assets/css/variables'
.c-votationModal
  &__counter
    display flex
    justify-content center
    margin 0 auto
    & >>> h2
      font-size 35px!important
  &__choosed
    color orange-dark!important
  &__buttons
    width 100%
    display grid
    grid-gap 10px
    grid-template-columns repeat(2, 1fr)
</style>
