const id = localStorage.user || ''
export default {
  state: { id },
  actions: {
    create({ commit }, name) {
      return this._vm.$db
        .collection('users')
        .add({
          name,
          score: {},
          round: {
            num: 0
          }
        })
        .then((user) => {
          commit('setUser', user.id)
          return Promise.resolve(user)
        })
    }
  },
  mutations: {
    setUser(state, user) {
      localStorage.user = user
      state.id = user
    }
  }
}
