<template>
  <div class="c-container" :class="styles">
    <div class="c-container__content">
      <slot />
    </div>
  </div>
</template>
<script>
export default {
  name: 'Container',
  props: {
    flex: {
      type: Boolean,
      default: false
    },
    medium: {
      type: Boolean,
      default: false
    },
    small: {
      type: Boolean,
      default: false
    },
    tiny: {
      type: Boolean,
      default: false
    },
    article: {
      type: Boolean,
      default: false
    }
  },
  computed: {
    styles() {
      return {
        'v--medium': this.medium,
        'v--small': this.small,
        'v--tiny': this.tiny,
        'v--flex': this.flex,
        'v--article': this.article
      }
    }
  }
}
</script>
<style lang="stylus" src="./styles.styl" scoped />
