        ref="counter"
      />
      <Round
        v-tooltip="'Contador de rounds'"
        v-if="room.round"
        :current="room.round.num"
        :total="room.roundLimit"
      />
    </div>
    <div class="c-gameHeader__right-container v--flex-align-right">
      <div class="c-gameHeader__settings">
        <Btn
          v-tooltip="'Configurar rounds ou banir pessoas'"
          v-if="room.owner && room.owner.id === userId"
          @click="$emit('settings')"
        >
          <icon icon-name="settings" />
        </Btn>
      </div>
      <SoundBtn />
      <LogoutBtn :room="room" />
      <Btn class="v--dark c-gameHeader__toggle" @click="$emit('toggle')">
