<template>
  <Modal class="v--box" :controllers="false" @close="$emit('close')">
    <div>
      <h2>
        {{ question }}
      </h2>
    </div>
    <template #footer>
      <Btn @click="getQuestion" class="v--info v--100">Outra dica</Btn>
    </template>
    <!-- <template #footer> </template> -->
  </Modal>
</template>
<script>
import questions from '@/defaults/questions'
import { randomizeArray } from '@/utils/randomize'
export default {
  name: 'QuestionModal',
  data() {
    return {
      questions: questions || [],
      usedQuestions: [],
      question: ''
    }
  },
  mounted() {
    this.getQuestion()
  },
  methods: {
    getQuestion() {
      const random = randomizeArray(
        this.questions.filter(
          (question) => !this.usedQuestions.includes(question)
        ),
        1
      )
      this.question = random[0]
        ? random[0]
        : 'Você consultou todas as perguntas disponíveis! :('
      this.usedQuestions.push(this.question)
      return this.question
    }
  }
}
</script>
