<template>
  <Modal
    @close="$emit('close')"
    @save="logout"
    positive-button-label="Sim, quero sair!"
  >
    <h2>Tem certeza que deseja sair? Sua pontuação será perdida.</h2>
  </Modal>
</template>
<script>
import { mapState } from 'vuex'

export default {
  props: {
    room: {
      type: Object,
      default: () => ({})
    },
    winOnSpyFall: {
      type: Boolean,
      default: true
    }
  },
  computed: {
    ...mapState({
      roomId: (state) => state.room.id,
      userId: (state) => state.user.id
    })
  },
  methods: {
    logout() {
      if (this.room && this.room.spy) {
        if (this.room.spy === this.userId && this.winOnSpyFall) {
          this.$store.dispatch('game/spyFall', this.room).then(() => {
            return this.goOut()
          })
        } else {
          this.goOut()
        }
      }
    },
    goOut() {
      this.$store.dispatch('room/removeUser').finally(() => {
        this.$emit('close')
        localStorage.clear()
        this.$router.push({ path: '/' })
      })
    }
  }
}
</script>
