<template>
  <div
    class="c-table"
    :style="{ 'background-image': `url(${getImgUrl('table_bg.png')})` }"
  >
    <Card
      v-for="card in items"
      :key="`round-${round}_${card.image}`"
      :title="card.title"
      :image="card.image"
      :show-buttons="showButtons"
      @vote="$emit('vote', $event)"
    />
  </div>
</template>

<script>
export default {
  name: 'Table',
  props: {
    cards: {
      type: Array,
      default: () => []
    },
    showButtons: {
      type: Boolean,
      default: false
    },
    round: {
      type: Number,
      default: 1
    }
  },
  data() {
    return {
      items: []
    }
  },
  created() {
    const array = this.cards
    let m = array.length
    let t = null
    let i = null

    // While there remain elements to shuffle…
    while (m) {
      // Pick a remaining element…
      i = Math.floor(Math.random() * m--)

      // And swap it with the current element.
      t = array[m]
      array[m] = array[i]
      array[i] = t
    }
    this.items = array
  },
  methods: {
    getImgUrl(image) {
      var images = require.context('@/assets/images', false, /\.(png|jpg)$/)
      return images('./' + image)
    }
  }
}
</script>

<style lang="stylus" scoped>
