<template>
  <Modal class="c-podiumModal" :controllers="false" :closeable="false">
    <Btn
      v-tooltip="'Configurar rounds ou banir pessoas'"
      class="c-podiumModal__settings"
      v-if="room.owner && room.owner.id === userId"
      @click="$emit('settings')"
    >
      <icon icon-name="settings" />
    </Btn>
    <Btn class="c-podiumModal__out" @click="goLeft">
      Sair do jogo
    </Btn>
    <carousel :per-page="1" :paginationPadding="4" :navigationEnabled="true">
      <slide>
        <div class="v--flex v--flex-align-middle v--flex-direction-column">
          <h1 class="v--text-center">{{ winners }}</h1>
          <lottie-animation
            v-show="isTeamWin() && !isSpy()"
            ref="team-win"
            style="height: 180px;"
            path="./animations/team-win.json"
          />
          <lottie-animation
            v-show="isSpyWin() && isSpy()"
            ref="spy-win"
            style="height: 180px;"
            path="./animations/cat-spy.json"
          />
          <lottie-animation
            v-show="youLose()"
            ref="spy-win"
            style="height: 180px;"
            path="./animations/sad.json"
          />
          <div v-if="!next && room.round" class="v--text-center">
            <p>
              O infiltrado era <b>{{ spyName() }}</b>
            </p>
            <p>
              A carta da rodada era <b>{{ room.round.card.title }}</b>
            </p>
            <p v-if="room.round && room.round.spyChoose">
              O infiltrado escolheu <b>{{ room.round.spyChoose }}</b>
            </p>
          </div>
        </div>
      </slide>
      <slide>
        <h1 class="v--text-center" style="margin-bottom: 8px;">Placar</h1>
        <ScoreBoard
          :users="room.users"
          :round="room.round"
          :showTitle="false"
        />
      </slide>
    </carousel>
    <template #footer>
      <div class="v--100">
        <Btn
          v-if="room.owner && room.owner.id === userId"
          class="v--success v--larger v--block"
          @click="callAction"
        >
          {{ btnText }}
        </Btn>
      </div>
    </template>
  </Modal>
</template>
<script>
import { mapState } from 'vuex'
import ScoreBoard from '@/components/ScoreBoard'
import { Carousel, Slide } from 'vue-carousel'
import gameConfig from '@/defaults/game-config'
export default {
  name: 'PodiumModal',
  props: {
    room: {
      type: Object,
      default: () => ({})
    }
  },
  data() {
    return {
      next: false
    }
  },
  components: {
    ScoreBoard,
    Carousel,
    Slide
  },
  computed: {
    ...mapState({
      userId: (state) => state.user.id
    }),
    winners() {
      if (this.youLose()) {
        return 'Você perdeu!'
      }
      if (this.room.status === gameConfig.status.podiumTeamSpyFall) {
        return 'O infiltrado desistiu! Vitória da equipe!'
      }
      if (this.room.status === gameConfig.status.podiumTeam) {
        return 'Vitória da equipe!!'
      }
      if (this.room.status === gameConfig.status.podiumSpy) {
        return 'Parabéns infiltrado!'
      }
      return '...'
    },
    btnText() {
      return this.room.round && this.room.round.num >= this.room.roundLimit
        ? 'Novo jogo'
        : 'Iniciar novo round'
    }
  },
  methods: {
    isSpyWin() {
      return this.room.status === gameConfig.status.podiumSpy
    },
    isTeamWin() {
      return (
        this.room.status === gameConfig.status.podiumTeam ||
        this.room.status === gameConfig.status.podiumTeamSpyFall
      )
    },
    isSpy() {
      return this.room && this.room.spy && this.userId === this.room.spy
    },
    youLose() {
      return (
        (this.isSpyWin() && !this.isSpy()) || (this.isSpy() && this.isTeamWin())
      )
    },
    spyName() {
      if (this.room && this.room.round) {
        const spy = this.room.users.find(
          (user) =>
            user &&
            user.id === this.room.spy &&
            user.round &&
            user.round.num === this.room.round.num
        )
        return spy ? spy.name : '...'
      }
      return '...'
    },
    goLeft() {
      this.$emit('exit-game')
    },
    callAction() {
      this.next = true
      if (this.room.round.num >= this.room.roundLimit) {
        this.$emit('new-game')
      } else {
        this.$emit('new-round')
      }
    }
  }
}
</script>
<style lang="stylus" scoped>
.c-podiumModal
  & >>> .VueCarousel-dot-container
    margin-top 0!important
  & >>> .VueCarousel-dot .VueCarousel-dot--active
    margin-top 0!important
  &__out
    position absolute!important
    right 20px
    top 20px
  &__settings
    position absolute!important
    left 20px
    top 20px
</style>
