<template>
  <div class="c-currentCard">
    <h2 class="c-currentCard__title">{{ title }}</h2>
    <Card
      :ref="item.title"
      :title="item.title"
      :image="item.image"
      :subtitle="item.role"
      :clickable="false"
    />
  </div>
</template>

<script>
export default {
  name: 'CurrentCard',
  props: {
    card: {
      type: Object,
      default: () => ({})
    }
  },
  computed: {
    title() {
      return this.card &&
        this.card.title &&
        this.card.image !== 'infiltrado.jpg'
        ? 'A carta da rodada é...'
        : 'Shhhhh!'
    },
    item() {
      return this.card && this.card.title
        ? this.card
        : {
            title: 'Carregando...'
            // image: 'espiao.jpg',
          }
    }
  },
  methods: {
    getImgUrl(image) {
      var images = require.context(
        '@/assets/images/cards',
        false,
        /\.(png|jpg)$/
      )
      return images('./' + image)
    }
  }
}
</script>

