<template>
  <div class="p-game">
    <div class="p-game__platform">
      <GameHeader
        v-if="userExists()"
        ref="header"
        :room="room"
        @toggle="toggleScoreBoard"
        @timeout="openFinalVotation"
        @settings="controlSettingsModal(true)"
      />
      <SpyHelp v-if="verifyRound() && isSpy()" />
      <div v-if="verifyRound()" class="p-game__body">
        <Table
          class="p-game__table"
          :cards="room.cards"
          :round="room.round.num"
          :show-buttons="isSpy()"
          @vote="spyVote"
        />
        <ScoreBoard
          class="p-game__score"
          :class="[{ 'p-game__score--invisible': showScoreBoard }]"
          :can-vote="!wasInterruptor()"
          :is-spy="isSpy()"
          :users="room.users"
          :round="room.round"
          show-buttons
          @open-votation="interrupt"
        >
          <template #footer>
            <Btn class="v--grey" @click="controlQuestionModal(true)">
              Dica de pergunta
            </Btn>
          </template>
        </ScoreBoard>
        <CurrentCard :card="user.round.card" />
      </div>
      <div v-else-if="!$wait.is('loading')">
        <lottie-animation
          style="height: 200px; margin: 0 auto;"
          path="./animations/spy.json"
        />
        <div class="v--text-center" v-if="userExists()">
          <h3>
            Relaxe! Você está aguardando <br />
            o novo round começar!
          </h3>
        </div>
        <h3 class="v--text-center" v-else>
          :( Você está fora!
        </h3>
        <div style="margin-top: 16px;" class="v--flex v--flex-align-center">
          <Btn
            v-if="isOwner()"
            class="v--success v--larger"
            @click="callAction"
          >
            {{ btnText }}
          </Btn>
        </div>
      </div>
      <portal to="modal">
        <FinalVotationModal
          v-if="hasRoomUserStatusAndRound() && hasFinalVotation()"
          :is-spy="isSpy()"
          :users="room.users"
          :computed-vote="hasFinalVote()"
          :uid="`f-${room.round.num}`"
          :end-time="room.finalVotationEnd"
          :room="room"
          @vote="finalVote"
          @timeout="computeScore"
        />

        <PodiumModal
          v-if="hasRoomUserStatusAndRound() && hasPodium() && canSeePodium()"
          :room="room"
          @new-round="nextRound"
          @new-game="newGame"
          @exit-game="controlLogoutModal(true)"
          @settings="controlSettingsModal(true)"
        />

        <InterruptionModal
          v-if="hasRoomUserStatusAndRound() && hasInterruption()"
          :users="room.users"
          :round="room.round"
          :votation-end="room.votationEnd"
          :is-spy="isSpy()"
          @no-votation="noVotation"
          @yes-votation="yesVotation"
          @timeout="timeoutVotation"
        />
        <LogoutModal
          v-if="showLogoutModal"
          :room="room"
          :win-on-spy-fall="false"
          @close="controlLogoutModal(false)"
        />
        <QuestionModal
          v-if="isRunning() && showQuestionModal"
          @close="controlQuestionModal(false)"
        />
        <Loading v-if="$wait.is('loading')" />
      </portal>
      <portal to="settings">
        <SettingsModal
          v-if="showSettingsModal && room.owner && room.owner.id === userId"
          @close="controlSettingsModal(false)"
          :room="room"
        />
      </portal>
    </div>
  </div>
</template>

<script>
import ScoreBoard from '@/components/ScoreBoard'
import GameHeader from '@/components/GameHeader'
import CurrentCard from '@/components/CurrentCard'
import Table from '@/components/Table'
import { db } from '@/plugins/db'
import gameConfig from '@/defaults/game-config'
import PodiumModal from '@/components/modals/PodiumModal'
import FinalVotationModal from '@/components/modals/FinalVotationModal'
import InterruptionModal from '@/components/modals/InterruptionModal'
import QuestionModal from '@/components/modals/QuestionModal'
import LogoutModal from '@/components/modals/LogoutModal'
import SettingsModal from '@/components/modals/SettingsModal'

import SpyHelp from '@/components/SpyHelp'
import { mapState } from 'vuex'
export default {
  route: '/jogo',
  name: 'Game',
  components: {
    SettingsModal,
    LogoutModal,
    FinalVotationModal,
    InterruptionModal,
    QuestionModal,
    PodiumModal,
    GameHeader,
    SpyHelp,
    Table,
    ScoreBoard,
    CurrentCard
  },
  data() {
    return {
      room: {},
      user: {},
      canVote: true,
      showScoreBoard: true,
      lastRound: false,
      roundEnded: false,
      dangerousClick: true,

      showSettingsModal: false,
      showQuestionModal: false,
      showLogoutModal: false
    }
  },
  watch: {
    /**
     * Quando todos terminam de votar no fim do round
     */
    'room.round.votes'() {
      const everyoneVote =
        this.room.users.filter(
          (user) => user.round && user.round.num === this.room.round.num
        ).length -
          1 ===
        Object.keys(this.room.round.votes).length

      if (
        everyoneVote &&
        this.room.status === gameConfig.status.finalVotation
      ) {
        this.computeScore()
      }
    },

    /**
     * Quando todos votam sim em uma votação
     */
    'room.round.votations'() {
      if (
        this.room.round.votationOwner &&
        this.room.round.votations[this.room.round.votationOwner]
      ) {
        const votes = Object.keys(
          this.room.round.votations[this.room.round.votationOwner].votes
        )
        const everyoneVote =
          this.room.users.filter(
            (user) => user.round && user.round.num === this.room.round.num
          ).length -
            1 ===
          votes.length

        if (everyoneVote && this.hasInterruption()) {
          this.$wait.start('loading')
          this.calcScoreFromVotations().finally(() => {
            this.$wait.end('loading')
          })
        }
      }
    },

    /**
     * Quando uma votação é recusada
     */
    'room.round.lastVotationDecliner'(newValue) {
      if (!newValue && this.$refs.header && this.$refs.header.$refs.counter) {
        this.$toast.success('O cronômetro foi pausado!')
        this.$refs.header.$refs.counter.pause()
      } else if (this.$refs.header && this.$refs.header.$refs.counter) {
        this.$refs.header.$refs.counter.play()
        if (this.room.round.lastVotationDecliner === '__cron$cron') {
          this.$toast.error('A votação expirou! O jogo continua!')
        } else {
          this.$toast.error(
            `<b>${this.room.round.lastVotationDecliner}</b> recusou a votação! O jogo continua!`
          )
        }
      }
    }
  },
  computed: {
    ...mapState({
      userId: (state) => state.user.id,
      roomId: (state) => state.room.id
    }),
    gameConfig: () => {
      return gameConfig
    },
    btnText() {
      return this.room.round && this.room.round.num >= this.room.roundLimit
        ? 'Novo jogo'
        : 'Iniciar novo round'
    }
  },
  created() {
    this.$wait.start('loading')

    if (this.roomId !== undefined) {
      this.$bind('room', db.collection('rooms').doc(this.roomId))
        .then(() => {
          return this.$bind('user', db.collection('users').doc(this.userId))
        })
        .finally(() => {
          if (!this.user || !this.user.id) {
            this.backToRoom()
          }
          this.$wait.end('loading')
        })
    } else {
      this.backToRoom()
    }
  },
  methods: {
    backToRoom() {
      if (this.$route.query.room) {
        this.$router.push({ path: `/room/${this.$route.query.room}` })
        this.$wait.end('loading')
        return
      } else {
        this.$route.query.push({ path: '/' })
        this.$wait.end('loading')
        return
      }
    },
    ////// SOBRE O ROUND //////
    isScoreable() {
      return (
        this.room &&
        this.room.status &&
        gameConfig.check.isScoreable(this.room.status)
      )
    },
    isPlayable() {
      return (
        this.room &&
        this.room.status &&
        gameConfig.check.isPlayable(this.room.status)
      )
    },
    verifyRound() {
      return this.hasRoomUserStatusAndRound() && this.isPlayable()
    },
    isRunning() {
      return this.room.status === gameConfig.status.running
    },
    hasFinalVotation() {
      return this.room.status === gameConfig.status.finalVotation
    },
    hasPodium() {
      return gameConfig.check.isPodium(this.room.status)
    },
    hasInterruption() {
      return (
        this.room.status === gameConfig.status.interruption &&
        this.room.round.votationOwner
      )
    },

    canSeePodium() {
      return !(
        (this.room.status === gameConfig.status.podiumTeamSpyFall &&
          this.isSpy()) ||
        !this.user ||
        !this.user.id
      )
    },

    hasRoomUserStatusAndRound() {
      return (
        this.user &&
        this.room &&
        this.room.status &&
        this.room.round &&
        this.user.round &&
        this.user.round.num === this.room.round.num
      )
    },

    userExists() {
      return (
        this.user &&
        this.room &&
        this.room.status &&
        this.user.id &&
        this.room.users.find((user) => user.id === this.user.id)
      )
    },

    wasInterruptor() {
      return (
        this.hasRoomUserStatusAndRound() &&
        this.room.round.votations &&
        this.room.round.votations[this.user.id]
      )
    },

    hasFinalVote() {
      return (
        this.hasRoomUserStatusAndRound() &&
        this.room.round.votes[this.user.id] !== undefined &&
        this.room.round.votes[this.user.id] !== null
      )
    },

    ////// SOBRE O USUÁRIO //////

    isSpy() {
      return (
        this.hasRoomUserStatusAndRound() &&
        this.user.round.card.image === gameConfig.objects.spyCard.image
      )
    },
    isOwner() {
      return this.user && this.user.id && this.user.id === this.room.owner.id
    },

    ////// SOBRE OS MODALS //////

    controlSettingsModal(value) {
      this.showSettingsModal = value
    },
    controlQuestionModal(option) {
      this.showQuestionModal = option
    },
    controlLogoutModal(option) {
      this.showLogoutModal = option
    },
    toggleScoreBoard() {
      this.showScoreBoard = !this.showScoreBoard
    },

    // SOBRE O JOGO
    openFinalVotation() {
      this.$wait.start('loading')
      this.$store.dispatch('game/finalVotation').finally(() => {
        this.$wait.end('loading')
      })
    },

    finalVote(user) {
      this.$wait.start('loading')
      if (!this.isSpy()) {
        this.$store.dispatch('game/finalVotationVote', user).finally(() => {
          this.$wait.end('loading')
        })
      }
    },

    computeScore() {
      this.$wait.start('loading')
      if (this.isScoreable()) {
        return this.$store
          .dispatch('game/stopTheGame')
          .then(() => {
            let sum = 0
            const votes = Object.keys(this.room.round.votes)

            votes.forEach((key) => {
              if (this.room.round.votes[key]) {
                sum++
              }
            })

            if (sum <= votes.length / 2 || sum === 0) {
              return this.$store.dispatch('game/spyPoint', this.room)
            } else {
              return this.$store.dispatch('game/teamPoint', this.room)
            }
          })
          .finally(() => {
            this.$wait.end('loading')
          })
      } else {
        this.$wait.end('loading')
      }
    },
    newGame() {
      this.$wait.start('loading')

      this.$store.dispatch('game/newGame').finally(() => {
        this.$wait.end('loading')
      })
    },
    nextRound() {
      this.$wait.start('loading')

      this.$store.dispatch('game/nextRound').finally(() => {
        this.$wait.end('loading')
      })
    },
    interrupt(target) {
      this.$wait.start('loading')

      this.$store
        .dispatch('game/interruptRound', target.id)
        .then(() => {
          this.canVote = false
        })
        .catch(() => {
          this.$toast.info('Alguém pediu antes! Tente novamente depois!')
        })
        .finally(() => {
          this.$wait.end('loading')
        })
    },
    yesVotation() {
      this.$wait.start('loading')

      this.$store.dispatch('game/yesVotation').finally(() => {
        this.$wait.end('loading')
      })
    },
    noVotation() {
      this.$wait.start('loading')

      this.$store.dispatch('game/noVotation', this.user.name).finally(() => {
        this.$wait.end('loading')
      })
    },
    timeoutVotation() {
      this.$wait.start('loading')

      this.$store.dispatch('game/noVotation', '__cron$cron').finally(() => {
        this.$wait.end('loading')
      })
    },
    spyVote(e) {
      this.$wait.start('loading')

      if (e === this.room.round.card.title) {
        this.$store
          .dispatch('game/spyPointPlus', {
            spy: this.room.spy,
            round: this.room.round,
            spyChoose: e
          })
          .finally(() => {
            this.$wait.end('loading')
          })
      } else {
        this.$store
          .dispatch('game/teamPointFromSpyError', {
            users: this.room.users,
            round: this.room.round,
            spy: this.room.spy,
            spyChoose: e
          })
          .finally(() => {
            this.$wait.end('loading')
          })
      }
    },
    calcScoreFromVotations() {
      if (this.hasInterruption()) {
        return this.$store.dispatch('game/stopTheGame', this.room).then(() => {
          if (
            this.room.spy ===
            this.room.round.votations[this.room.round.votationOwner].target
          ) {
            return this.$store.dispatch('game/teamPointFromVotation', this.room)
          } else {
            return this.$store.dispatch('game/spyPoint', this.room)
          }
        })
      }
    },
    callAction() {
      this.next = true
      if (this.room.round.num >= this.room.roundLimit) {
        this.newGame()
      } else {
        this.nextRound()
      }
    }
  },
  head() {
    return {
      title: 'O infiltrado - jogo'
    }
  }
}
</script>
