    position fixed
    top 0
    left 0
    width 100%
    background-color dark
    z-index 12
  &__topbar
    padding 20px
    display flex
    align-items center
    justify-content center
    height 80px
    @media (max-width 768px)
      justify-content flex-start
  &__body
    display flex
    align-items flex-start
    padding 16px
    height 100%
    @media (max-width 768px)
      padding 4px
  &__table
    margin-right 20px
    @media (max-width 768px)
      margin-right 0
      margin-bottom 150px
  &__score
    @media (min-width 769px)
      margin-bottom 200px
    @media (max-width 768px)
      z-index 8
      position fixed
      right 0
      top 80px
      box-shadow inset 0px 0px 0px 1px #000, 0px 0px 4px #000
    &-toggle
      display none!important
      @media (max-width 768px)
        display flex!important
        position absolute!important
        top 20px
        right 4px
    &--invisible
      @media (max-width 768px)
        display none
  >>> .c-countdown
    @media (max-width 768px)
      justify-content flex-start!important
      text-align left
      h2
        font-size 28px
        width auto
  >>> .c-round
    @media (max-width 768px)
