import { randomizeArray } from '@/utils/randomize'
import cards from '@/defaults/cards'
import gameConfig from '@/defaults/game-config'

const getDeck = () => {
  return randomizeArray(cards, gameConfig.numberOf.cards)
}

const getRoundCard = (deck) => {
  return randomizeArray(deck, gameConfig.numberOf.roundCard)
}

const getKey = () => {
  return randomizeArray([3, 2, 1], 1)
}

export default {
  actions: {
    nextRound({ rootState }) {
      const deck = getDeck()
      const roundCard = getRoundCard(deck)[0]

      let batch = this._vm.$db.batch()
      const roomSfRef = this._vm.$db.collection('rooms').doc(rootState.room.id)

      return roomSfRef.get().then((obj) => {
        // Get current room data
        const room = obj.data()
        // Generates a spy
        const spy = randomizeArray(
          room.users,
          gameConfig.numberOf.spy,
          room.spy,
          room.key,
          room.round.num
        )[0]

        const nextRound = room.round.num + 1

        batch.update(roomSfRef, {
          status: gameConfig.status.running,
          cards: deck,
          spy: spy.id,
          votationStart: 0,
          votationEnd: 0,
          finalVotationEnd: 0,
          round: {
            spyChoose: '',
            num: nextRound,
            card: roundCard,
            votationOwner: '',
            votations: {},
            endTime: Date.now() + room.roundTime * 60000,
            votes: {},
            winner: 0
          }
        })

        // The round card will be getted from user
        room.users.forEach((user) => {
          const sfRef = this._vm.$db.collection('users').doc(user.id)
          batch.update(sfRef, {
            round: {
              num: nextRound,
              card: user.id === spy.id ? gameConfig.objects.spyCard : roundCard
            }
          })
        })

        return batch.commit()
      })
    },
    newGame({ rootState }) {
      const deck = getDeck()
      const roundCard = getRoundCard(deck)[0]

      let batch = this._vm.$db.batch()
      const roomSfRef = this._vm.$db.collection('rooms').doc(rootState.room.id)

      return roomSfRef.get().then((obj) => {
        // Get current room data
        const room = obj.data()
        // Generates a spy
        const spy = randomizeArray(
          room.users,
          gameConfig.numberOf.spy,
          room.spy
        )[0]

        batch.update(roomSfRef, {
          key: getKey()[0],
          status: gameConfig.status.running,
          cards: deck,
          spy: spy.id,
          votationStart: 0,
          votationEnd: 0,
          finalVotationEnd: 0,
          round: {
            spyChoose: '',
            num: 1,
            card: roundCard,
            votationOwner: '',
            votations: {},
            endTime: Date.now() + room.roundTime * 60000,
            votes: {},
            winner: 0
          }
        })

        room.users.forEach((user) => {
          const sfRef = this._vm.$db.collection('users').doc(user.id)
          batch.update(sfRef, {
            score: {},
            round: {
              num: 1,
              card: user.id === spy.id ? gameConfig.objects.spyCard : roundCard
            }
          })
        })

        return batch.commit()
      })
    },

    /**
     * Momento em que a votação é aberta por tempo ter esgotado.
     */
    finalVotation({ dispatch, rootState }) {
      return dispatch(
        'room/updateRoom',
        {
          id: rootState.room.id,
          call: (data, transaction, doc) => {
            if (data.status !== gameConfig.status.podium) {
              transaction.update(doc, {
                status: gameConfig.status.finalVotation,
                finalVotationEnd:
                  Date.now() + gameConfig.times.finalVotation * 60000
              })
            }
          }
        },
        {
          root: true
        }
      )
    },

    /**
     * Momento em que o resultado do round é exibido
     */
    goToPodium({ dispatch, rootState }) {
      return dispatch(
        'room/updateRoom',
        {
          id: rootState.room.id,
          status: gameConfig.status.podium
        },
        {
          root: true
        }
      )
    },

    /**
     * Momento em que uma votação é aberta no meio do jogo
     */
    interruptRound({ dispatch, rootState }, target) {
      const userId = rootState.user.id

      return dispatch(
        'room/updateRoom',
        {
          id: rootState.room.id,
          call: (data, transaction, doc) => {
            if (data.status === gameConfig.status.running) {
              transaction.update(doc, {
                status: gameConfig.status.interruption,
                [`round.votationOwner`]: userId,
                votationStart: Date.now(),
                votationEnd: Date.now() + gameConfig.times.interruption * 60000,
                [`round.lastVotationDecliner`]: '',
                [`round.votations.${userId}`]: {
                  target,
                  votes: {
                    [userId]: data.spy.id === target
                  }
                }
              })
            }
          }
        },
        { root: true }
      )
    },

    /**
     * Momento em que uma votação de meio de jogo é rejeitada
     */
    cancelInterruption({ dispatch, rootState }) {
      return dispatch(
        'room/updateRoom',
        {
          id: rootState.room.id,
          call: (data, transaction, doc) => {
            if (data.status === gameConfig.status.interruption) {
              transaction.update(doc, {
                votationOwner: '',
                votationEnd: 0,
                votationStart: 0,
                endRound:
                  parseInt(data.endRound) +
                  (Date.now() - parseInt(data.votationStart)),
                status: gameConfig.status.running
              })
            }
          }
        },
        {
          root: true
        }
      )
    },

    /**
     *
     */
    stopTheGame({ dispatch, rootState }) {
      return dispatch(
        'room/updateRoom',
        {
          id: rootState.room.id,
          call: (data, transaction, sfDocRef) => {
            if (gameConfig.check.isComputable(data.status)) {
              return transaction.update(sfDocRef, {
                status: gameConfig.status.computing
              })
            }
          }
        },
        {
          root: true
        }
      )
    },

    /**
     * Quando um usuário declina uma votação de meio de jogo
     */
    noVotation({ dispatch, rootState }, decliner) {
      return dispatch(
        'room/updateRoom',
        {
          id: rootState.room.id,
          call: (data, transaction, doc) => {
            if (data.status === gameConfig.status.interruption) {
              transaction.update(doc, {
                [`round.lastVotationDecliner`]: decliner,
                [`round.votationOwner`]: '',
                [`round.endTime`]:
                  parseInt(data.round.endTime) +
                  (Date.now() - parseInt(data.votationStart)),
                votationEnd: 0,
                votationStart: 0,
                status: gameConfig.status.running
              })
            }
          }
        },
        { root: true }
      )
    },

    /**
     * Vota sim, em votação do meio do jogo
     */
    yesVotation({ dispatch, rootState }) {
      return dispatch(
        'room/updateRoom',
        {
          id: rootState.room.id,
          call: (data, transaction, doc) => {
            if (data.status === gameConfig.status.interruption) {
              return transaction.update(doc, {
                [`round.votations.${data.round.votationOwner}.votes.${rootState.user.id}`]:
                  data.spy ===
                  data.round.votations[data.round.votationOwner].target
              })
            }
          }
        },
        {
          root: true
        }
      )
    },
    /**
     * Voto para votação do fim do round
     *
     */
    finalVotationVote({ dispatch, rootState }, user) {
      return dispatch(
        'room/updateRoom',
        {
          id: rootState.room.id,
          call: (data, transaction, sfDocRef) => {
            if (data.status === gameConfig.status.finalVotation) {
              transaction.update(sfDocRef, {
                [`round.votes.${rootState.user.id}`]: user.id === data.spy
              })
            }
          }
        },
        { root: true }
      )
    },

    /**
     * Quando o infiltrado acerta a carta da rodada
     */
    spyPointPlus({ rootState }, { round, spyChoose }) {
      let batch = this._vm.$db.batch()
      const userSfRef = this._vm.$db.collection('users').doc(rootState.user.id)

      batch.update(userSfRef, {
        [`score.${round.num}`]: gameConfig.points.spyVote
      })

      const roomSfRef = this._vm.$db.collection('rooms').doc(rootState.room.id)

      batch.update(roomSfRef, {
        status: gameConfig.status.podiumSpy,
        [`round.spyChoose`]: spyChoose
      })

      return batch.commit()
    },

    /**
     * Quando ninguém acerta quem é o infiltrado
     */
    spyPoint({ rootState }, { spy, round }) {
      let batch = this._vm.$db.batch()

      const roomSfRef = this._vm.$db.collection('rooms').doc(rootState.room.id)
      const userSfRef = this._vm.$db.collection('users').doc(spy)

      batch.update(userSfRef, {
        [`score.${round.num}`]: gameConfig.points.spy
      })

      batch.update(roomSfRef, {
        status: gameConfig.status.podiumSpy
      })

      return batch.commit()
    },

    /**
     * Quando o time acerta a votação final
     */
    teamPoint({ rootState }, { spy, round, users }) {
      let batch = this._vm.$db.batch()
      const valids = Object.keys(round.votes).filter((key) => round.votes[key])

      users.forEach((user) => {
        if (user.id !== spy && valids.includes(user.id)) {
          const sfRef = this._vm.$db.collection('users').doc(user.id)
          batch.update(sfRef, {
            [`score.${round.num}`]: gameConfig.points.team
          })
        }
      })

      const roomSfRef = this._vm.$db.collection('rooms').doc(rootState.room.id)
      batch.update(roomSfRef, {
        status: gameConfig.status.podiumTeam
      })
      return batch.commit()
    },

    /**
     * Quando o infiltrado vota errado
     */
    teamPointFromSpyError({ rootState }, { users, round, spy, spyChoose }) {
      let batch = this._vm.$db.batch()

      users.forEach((user) => {
        if (user.id !== spy) {
          const sfRef = this._vm.$db.collection('users').doc(user.id)
          batch.update(sfRef, {
            [`score.${round.num}`]: gameConfig.points.team
          })
        }
      })

      const roomSfRef = this._vm.$db.collection('rooms').doc(rootState.room.id)
      batch.update(roomSfRef, {
        status: gameConfig.status.podiumTeam,
        [`round.spyChoose`]: spyChoose
      })
      return batch.commit()
    },

    /**
     * Quando o infiltrado desiste
     */
    spyFall({ rootState }, { users, round, spy }) {
      let batch = this._vm.$db.batch()

      users.forEach((user) => {
        if (user.id !== spy) {
          const sfRef = this._vm.$db.collection('users').doc(user.id)
          batch.update(sfRef, {
            [`score.${round.num}`]: gameConfig.points.team
          })
        }
      })

      const roomSfRef = this._vm.$db.collection('rooms').doc(rootState.room.id)
      batch.update(roomSfRef, {
        status: gameConfig.status.podiumTeamSpyFall,
        [`round.spyChoose`]: ''
      })
      return batch.commit()
    },

    /**
     * Quando o time acerta uma votaç~ao
     */
    teamPointFromVotation({ rootState }, { spy, round, users }) {
      let batch = this._vm.$db.batch()

      users.forEach((user) => {
        if (user.id !== spy) {
          const sfRef = this._vm.$db.collection('users').doc(user.id)
          batch.update(sfRef, {
            [`score.${round.num}`]:
              round && round.votationOwner === user.id
                ? gameConfig.points.teamVote
                : gameConfig.points.team
          })
        }
      })

      const roomSfRef = this._vm.$db.collection('rooms').doc(rootState.room.id)
      batch.update(roomSfRef, {
        status: gameConfig.status.podiumTeam
      })
      return batch.commit()
    }
  }
}
