<template>
  <div class="c-countdown">
    <h2 v-if="finalTime" :class="{ 'c-countdown--ending': ending }">
      {{ timeLeft }}
    </h2>
    <audio :ref="`${uid}-audio`">
      <source src="/sounds/clock.mp3" type="audio/mp3" />
    </audio>
  </div>
</template>

<script>
import { mapState } from 'vuex'
import { zeroPadded } from '@/utils/hour'
export default {
  name: 'Countdown',
  props: {
    roomStatus: {
      type: [Number, String],
      default: 0
    },
    finalTime: {
      type: [Number, String],
      default: 0
    },
    uid: {
      type: [Number, String],
      default: 0
    }
  },
  emits: ['timeout'],
  data() {
    return {
      startsFinishAt: 10,
      secondsLeft: 0,
      intervalTimer: 0,
      timeLeft: '00:00',
      ending: false
    }
  },
  computed: {
    ...mapState({
      sound: (state) => state.config.sound
    })
  },
  watch: {
    secondsLeft(newValue) {
      if (newValue === 0) {
        this.$emit('timeout')
      }
    },
    sound(newValue) {
      if (this.$refs[`${this.uid}-audio`]) {
        if (newValue && this.ending) {
          this.$refs[`${this.uid}-audio`].play()
        } else {
          this.$refs[`${this.uid}-audio`].pause()
        }
      }
    }
  },
  mounted() {
    this.start()
  },
  destroyed() {
    if (this.intervalTimer) {
      clearInterval(this.intervalTimer)
    }
  },
  methods: {
    start() {
      clearInterval(this.intervalTimer)
      this.countdown()
    },
    pause() {
      if (this.sound) {
        this.$refs[`${this.uid}-audio`].pause()
      }
      clearInterval(this.intervalTimer)
    },
    play() {
      if (this.secondsLeft < this.startsFinishAt && this.sound) {
        this.$refs[`${this.uid}-audio`].play()
      }
      this.start()
    },
    clockSong() {
      if (this.sound) {
        this.$refs[`${this.uid}-audio`].play()
      }
    },
    countdown() {
      this.intervalTimer = setInterval(() => {
        this.secondsLeft = Math.round(
          (parseInt(this.finalTime) - Date.now()) / 1000
        )

        if (this.secondsLeft < 0) {
          this.$emit('timeout')
          clearInterval(this.intervalTimer)
          return
        }
        if (this.secondsLeft === this.startsFinishAt) {
          this.ending = true
          this.clockSong()
        }
        this.displayTimeLeft(this.secondsLeft)
      }, 1000)
    },
    displayTimeLeft(secondsLeft) {
      const minutes = Math.floor((secondsLeft % 3600) / 60)
      const seconds = secondsLeft % 60

      this.timeLeft = `${zeroPadded(minutes)}:${zeroPadded(seconds)}`
    }
  }
}
</script>

<style lang="stylus" scoped>
@import '../../../assets/css/variables.styl'

.c-countdown
  h2
    width 200px
    font-size 60px
    text-align center
  &--ending
    color #e40e13!important
</style>
