<template>
  <div class="p-room">
    <Container class="v--text-center">
      <lottie-animation style="height: 70px;" path="./animations/spy.json" />
      <h1 class="p-room__title">Sala de espera</h1>
      <template v-if="room">
        <p>
          Serão <b>{{ room.roundLimit }}</b> rodadas de
          <b>{{ room.roundTime }}</b> minutos
        </p>
        <div
          v-if="room.round && room.round.num > 0"
          class="p-room__game-status"
        >
          <p>O jogo já está em andamento</p>
          <div v-if="!waitOwner">
            <p>
              Rodada <b>{{ room.round.num }}</b> de
              <b>{{ room.roundLimit }}</b> com tempo restante de:
            </p>
            <Countdown
              class="p-room__countdown"
              ref="counter"
              :final-time="room.round.endTime"
              :uid="`r-${room.round.num}`"
              @time-out="changeInfo"
            />
          </div>
          <div v-else>
            <p>
              Rodada <b>{{ room.round.num }}</b> de
              <b>{{ room.roundLimit }}</b>
            </p>
            <p>
              Aguardando apenas o dono da sala iniciar o próximo round.
            </p>
          </div>
        </div>
      </template>
      <div class="p-room__body">
        <CopyArea :value="roomId" />
        <div class="p-room__data">
          <WaitList :users="room.users" />
          <div v-if="room && room.owner && room.owner.id === userId">
            <Btn
              class="v--success v--large v--block"
              :disabled="!room.users || room.users.length < 3"
              @click="start"
            >
              Iniciar!
            </Btn>
            <span>São necessários 3 jogadores (mínimo) para começar.</span>
          </div>
          <span v-else-if="room.status !== gameConfig.status.running"
            >Aguardando o dono da sala ({{ room.owner && room.owner.name }})
            iniciar o jogo.</span
          >
        </div>
      </div>
    </Container>
    <portal to="modal">
      <WhatsYourNameModal
        v-if="showForm"
        nextLabel="Entrar na sala"
        v-model="userName"
        @next-step="enterRoom"
      />
      <Loading v-if="$wait.is('loading')" />
    </portal>
  </div>
</template>
<script>
import { mapState } from 'vuex'
import WaitList from '@/components/WaitList'
import WhatsYourNameModal from '@/components/modals/WhatsYourNameModal'
import gameConfig from '@/defaults/game-config'

export default {
  name: 'Room',
  components: { WaitList, WhatsYourNameModal },
  data() {
    return {
      waitOwner: false,
      room: {},
      user: null,
      showForm: false,
      userName: '',
      gameConfig
    }
  },
  computed: {
    ...mapState({
      userId: (state) => state.user.id,
      roomId: (state) => state.room.id
    })
  },
  beforeCreate() {
    if (!this.$route.params.id) {
      this.$router.push({ path: '/' })
    }

    if (!this.userId) {
      this.showForm = true
    }
  },
  created() {
    this.$wait.start('loading')

    this.$bind('room', this.$db.collection('rooms').doc(this.$route.params.id))
      .then(() => {
        if (this.room.id !== this.roomId) {
          this.$store.commit('room/setRoom', this.room.id)
        }
        this.canPlay()
      })
      .finally(() => {
        this.$wait.end('loading')
      })
  },
  destroyed() {
    this.waitOwner = false
  },
  watch: {
    '$route.query'() {
      this.$router.push({ path: '/' })
    },
    'room.status'(newValue) {
      if (newValue === gameConfig.status.running) {
        this.canPlay()
      } else if (newValue !== gameConfig.status.interruption) {
        this.waitOwner = true
      }
    }
  },
  methods: {
    changeInfo() {
      this.waitOwner = true
    },
    canPlay() {
      const user = this.room.users.find((user) => user.id === this.userId)

      if (!(this.roomId === this.room.id && this.userId && user)) {
        this.showForm = true
      } else if (this.room.status === gameConfig.status.running) {
        if (!this.user) {
          this.$bind(
            'user',
            this.$db.collection('users').doc(this.userId)
          ).then(() => {
            if (
              this.user.round &&
              this.room.round &&
              this.user.round.num === this.room.round.num
            ) {
              this.goToGame()
            }
          })
        } else if (
          this.room.round &&
          this.user.round &&
          this.user.round.num === this.room.round.num
        ) {
          this.goToGame()
        }
      } else {
        this.showForm = false
      }
    },
    goToGame() {
      this.$router.push({ path: '/jogo', query: { room: this.room.id } })
    },
    start() {
      this.$wait.start('loading')

      this.$store
        .dispatch('game/newGame')
        .then(() => {
          this.goToGame()
        })
        .finally(() => {
          this.$wait.end('loading')
        })
    },
    enterRoom() {
      this.$wait.start('loading')
      this.$store.commit('room/setRoom', this.room.id)
      const duplicate = this.room.users.find(
        (user) => user.name === this.userName
      )
      if (duplicate) {
        this.$toast.error('Já temos alguém com esse nome!')
        this.$wait.end('loading')
      } else if (this.userName === '__cron$cron') {
        this.$toast.error(
          'Infelizmente, esse nome está bloqueado. Tente outro!'
        )
      } else {
        this.$store
          .dispatch('room/addUser', this.userName)
          .then(() => {
            this.showForm = false
          })
          .finally(() => {
            this.$wait.end('loading')
          })
      }
    }
  },
  head() {
    return {
      title: 'O infiltrado - sala de espera'
    }
  }
}
</script>
