    padding 4px

  &__title
    font-size 32px
  &__body
    margin-top 16px
  &__data
    max-width 500px
    margin 0 auto
    padding 2px
  &__game-status
    margin-top: 10px
    font-size 20px
  &__countdown
    align-items center
    display flex
    justify-content center
</style>
