<template>
  <div class="p-landingpage">
    <Topbar />
    <Container medium>
      <Jumbotron
        title="Descubra quem é o infiltrado"
        description="Há alguém armando um plano para se manter infiltrado dentro do grupo. E aí? Você consegue adivinhar quem é?"
      >
        <template #buttons>
          <Btn
            class="v--mixed-yellow-orange v--rounded v--larger"
            @click="showModal(steps.secret)"
          >
            Entrar
          </Btn>
          <Btn
            class="v--mixed-yellow-orange v--rounded v--larger"
            @click="showModal(steps.userName)"
          >
            Criar nova sala
          </Btn>
        </template>
        <template #right-side>
          <lottie-animation path="./animations/spy.json" />
        </template>
      </Jumbotron>
      <portal to="modal">
        <template v-if="modal">
          <WhatsYourNameModal
            v-if="step === steps.userName"
            v-model="userName"
            @close="closeModal"
            @next-step="step = 2"
          />
          <RoomSettingsModal
            v-else-if="step === steps.settings"
            @previous-step="step = 1"
            @start-game="createRoom"
            @close="closeModal"
          />
          <RoomSecretModal
            v-else-if="step === steps.secret"
            @enter="enterRoom"
            @close="closeModal"
          />
        </template>
        <Loading v-if="$wait.is('loading')" />
      </portal>
    </Container>
    <Footer />
  </div>
</template>
<script>
import RoomSettingsModal from '@/components/modals/RoomSettingsModal'
import RoomSecretModal from '@/components/modals/RoomSecretModal'
import WhatsYourNameModal from '@/components/modals/WhatsYourNameModal'

export default {
  name: 'Index',
  components: {
    RoomSecretModal,
    RoomSettingsModal,
    WhatsYourNameModal
  },
  data() {
    return {
      steps: {
        userName: 1,
        settings: 2,
        secret: 3
      },
      step: 1,
      modal: false,
      rooms: [],
      userName: '',
      roomId: ''
    }
  },
  destroyed() {
    this.$wait.end('loading')
  },
  methods: {
    closeModal() {
      this.modal = false
    },
    showModal(step) {
      this.step = step
      this.modal = true
    },
    enterRoom(room) {
      this.$router.push({ path: 'room', query: { room } })
    },
    createRoom({ roundLimit, roundTime }) {
      this.$wait.start('loading')

      const payload = {
        roundLimit,
        roundTime,
        userName: this.userName
      }

      this.$store
        .dispatch('room/create', payload)
        .then((roomId) => {
          this.$router.push({ path: `/room/${roomId}` })
        })
        .finally(() => {
          this.$wait.end('loading')
        })
    }
  },
  head() {
    return {
      title: 'O infiltrado'
    }
  }
}
</script>

