<template>
  <Modal :controllers="false" @close="$emit('close')">
    <Field
      name="roundLimit"
      inputType="number"
      label="Nº de rodadas"
      v-model="roundLimit"
    />
    <Field
      name="roundTime"
      inputType="number"
      label="Duração de cada rodada"
      v-model="roundTime"
    />

    <template #footer>
      <div class="v--flex-align-right">
        <Btn @click="$emit('previous-step')">
          Voltar
        </Btn>
        <Btn class="v--success" @click="start">
          Criar sala
        </Btn>
      </div>
    </template>
  </Modal>
</template>
<script>
export default {
  name: 'RoomSettingsModal',
  data() {
    return {
      roundTime: 8,
      roundLimit: 5
    }
  },
  methods: {
    start() {
      this.$emit('start-game', {
        roundTime: parseInt(this.roundTime),
        roundLimit: parseInt(this.roundLimit)
      })
    }
  }
}
</script>
