<template>
  <Modal
    class="c-openVotationModal"
    :controllers="false"
    @close="$emit('close')"
  >
    <h2>
      Hmmm... {{ text }}
      <span class="c-openVotationModal__choosed">{{ target.name }}</span> é o
      infiltrado.<br />
      Deseja abrir votação?
    </h2>
    <template #footer>
      <div class="c-openVotationModal__buttons">
        <Btn class="v--block v--dark" @click="$emit('open-votation')">
          Sim
        </Btn>
        <Btn class="v--block v--dark" @click="$emit('cancel-votation')">
          Não
        </Btn>
      </div>
    </template>
  </Modal>
</template>
<script>
export default {
  name: 'OpenVotationModal',
  props: {
    isSpy: {
      type: Boolean,
      required: true
    },
    target: {
      type: Object,
      required: true
    }
  },
  computed: {
    text() {
      return this.isSpy ? 'você quer que todos acreditem que' : 'você acha que'
    }
  }
}
</script>
