const sound = localStorage.sound !== 'false'
export default {
  state: { sound },
  actions: {
    toggleSound({ commit, state }) {
      commit('setSound', !state.sound)
    }
  },
  mutations: {
    setSound(state, sound) {
      localStorage.sound = sound ? 'true' : 'false'
      state.sound = sound
    }
  }
}
