import { FieldValue } from '@/plugins/db'
import gameConfig from '@/defaults/game-config'

const id = localStorage.room

export default {
  state: {
    id
  },
  actions: {
    updateRoom(_, { id, status, call = null, object = null }) {
      var doc = this._vm.$db.collection('rooms').doc(id)
      return this._vm.$db.runTransaction((transaction) => {
        return transaction.get(doc).then((sfDoc) => {
          if (typeof call === 'function') {
            return call(sfDoc.data(), transaction, doc)
          } else if (object !== null) {
            return transaction.update(doc, object)
          } else if (sfDoc.data().status !== status) {
            return transaction.update(doc, {
              status
            })
          }
        })
      })
    },
    /**
     * Cria sala já com o owner
     */
    create({ commit, dispatch }, payload) {
      return dispatch('user/create', payload.userName, { root: true }).then(
        (owner) => {
          return this._vm.$db
            .collection('rooms')
            .add({
              owner,
              users: [owner],
              roundTime: payload.roundTime,
              roundLimit: payload.roundLimit,
              status: gameConfig.status.room
            })
            .then((room) => {
              commit('setRoom', room.id)
              return Promise.resolve(room.id)
            })
        }
      )
    },
    /**
     * Adiciona usuário à sala
     */
    addUser({ state, dispatch }, name) {
      return dispatch('user/create', name, { root: true }).then((user) => {
        return dispatch('updateRoom', {
          id: state.id,
          object: {
            users: FieldValue.arrayUnion(user)
          }
        }).then(() => {
          return Promise.resolve(user)
        })
      })
    },

    banUser({ dispatch, rootState }, id) {
      const user = this._vm.$db.collection('users').doc(id)
      return dispatch('updateRoom', {
        id: rootState.room.id,
        object: {
          users: FieldValue.arrayRemove(user)
        }
      }).then(() => {
        return user.delete()
      })
    },

    /**
     * Remove usuário logado da sala e da base
     */
    removeUser({ dispatch, commit, rootState }) {
      const user = this._vm.$db.collection('users').doc(rootState.user.id)

      return dispatch('updateRoom', {
        id: rootState.room.id,
        object: {
          users: FieldValue.arrayRemove(user)
        }
      }).then(() => {
        commit('user/setUser', '', { root: true })
        commit('setRoom', '')
        localStorage.clear()
        return user.delete()
      })
    }
  },
  mutations: {
    setRoom(state, room) {
      localStorage.room = room
      state.id = room
    }
  }
}
