<template>
  <div>
    <div class="c-field_container" :class="{ 'c-field_container--flex': flex ,flushed: flushed }">
      <div v-if="label" class="c-field_label">
        <label :for="name" v-html="label" />
      </div>
      <div class="c-field_box" :class="sizeStyles">
        <div v-if="loading" class="c-field_spinner">
          <svg
            class="c-field_icon"
            version="1.1"
            xmlns="http://www.w3.org/2000/svg"
            xmlns:xlink="http://www.w3.org/1999/xlink"
            x="0px"
            y="0px"
            width="40px"
            height="40px"
            viewBox="0 0 50 50"
            style="enable-background: new 0 0 50 50;"
            xml:space="preserve"
          >
            <path
              d="M43.935,25.145c0-10.318-8.364-18.683-18.683-18.683c-10.318,0-18.683,8.365-18.683,18.683h4.068c0-8.071,6.543-14.615,14.615-14.615c8.072,0,14.615,6.543,14.615,14.615H43.935z"
            >
              <animateTransform
                attributeType="xml"
                attributeName="transform"
                type="rotate"
                from="0 25 25"
                to="360 25 25"
                dur="1s"
                repeatCount="indefinite"
              />
            </path>
          </svg>
        </div>
        <the-mask
          v-if="mask"
          :id="name"
          ref="input"
          :name="name"
          :mask="mask"
          :autofocus="autofocus"
          :required="required"
          :type="fieldType"
          :value="value"
          :placeholder="placeholder"
          :disabled="disabled"
          class="c-field"
          :class="styles"
          @input="$emit('input', $event)"
          @blur="blur"
        />
        <input
          v-else-if="fieldType !== 'select'"
          :id="name"
          ref="input"
          :name="name"
          :autofocus="autofocus"
          :required="required"
          :mask="mask"
          :type="fieldType"
          :value="value"
          :readonly="readonly"
          :placeholder="placeholder"
          :disabled="disabled"
          class="c-field"
          :class="styles"
          @input="$emit('input', $event.target.value)"
          @blur="blur"
        />
        <select
          :id="name"
          :autofocus="autofocus"
          :name="name"
          :disabled="disabled"
          class="c-field"
          @change="change"
          @input="$emit('input', $event.target.value)"
          :class="{
            'v--rounded': rounded
          }"
          v-else
        >
          <option disabled class="c-field-option" value
            >-- Selecionar --</option
          >
          <option
            class="c-field-option"
            v-for="item in items"
            :key="item.value"
            :selected="item.selected || value === item.value"
            :value="item.value"
            v-html="item.label"
          />
        </select>
        <div
          v-if="helperControl"
          class="c-field_btn-control"
          :class="{ 'v--has-error': showErrorMessage }"
        >
          <Btn
            tabindex="-1"
            v-tooltip="helperMessage"
            rounded
            class="v--flushed"
            @click.prevent.stop
          >
            <icon icon-name="help-circle" />
          </Btn>
        </div>
        <div
          v-if="passwordControl"
          class="c-field_btn-control"
          :class="{ 'v--has-error': showErrorMessage }"
        >
          <Btn
            tabindex="-1"
            rounded
            class="v--flushed"
            @click.prevent="passwordVisible = !passwordVisible"
          >
            <icon size="20" :icon-name="passwordControlIcon" />
          </Btn>
        </div>
      </div>
    </div>
    <span v-if="showErrorMessage" class="c-field_message">{{
      errorMessage
    }}</span>
  </div>
</template>

<script>
export default {
  name: 'Field',
  props: {
    name: {
      type: String,
      default: 'name',
      required: true
    },
    items: {
      type: Array,
      default: () => []
    },
    label: {
      type: String,
      default: ''
    },
    placeholder: {
      type: String,
      default: ''
    },
    hasError: {
      type: Boolean,
      default: false
    },
    errorMessage: {
      type: String,
      default: 'Faltou preencher aqui.'
    },
    helperMessage: {
      type: String,
      default: ''
    },
    loading: {
      type: Boolean,
      default: false
    },
    mask: {
      type: [String, Array],
      default: ''
    },
    inputType: {
      type: String,
      default: 'text'
    },
    required: {
      type: Boolean,
      default: false
    },
    disabled: {
      type: Boolean,
      default: false
    },
    autofocus: {
      type: Boolean,
      default: false
    },
    value: {
      type: [String, Number],
      default: ''
    },
    showPasswordControl: {
      type: Boolean,
      default: true
    },
    readonly: {
      type: Boolean,
      default: false
    },
    flushed: {
      type: Boolean,
      default: false
    },
    flex: {
      type: Boolean,
      default: false
    },
    rounded: {
      type: Boolean,
      default: false
    },
    size: {
      type: String,
      default: 'medium',
      validator: (value) => {
        return ['small', 'medium', 'large'].indexOf(value) !== -1
      }
    }
  },
  data() {
    return {
      passwordVisible: false,
      isErrorChecked: null
    }
  },
  computed: {
    showErrorMessage() {
      return (
        (this.hasError && this.isErrorChecked === null) ||
        (this.isErrorChecked && this.hasError)
      )
    },
    helperControl() {
      return Boolean(this.helperMessage)
    },
    passwordControl() {
      return this.inputType === 'password' && this.showPasswordControl
    },
    passwordControlIcon() {
      return this.passwordVisible ? 'eye-off' : 'eye'
    },
    fieldType() {
      if (this.inputType === 'password' && this.passwordVisible) {
        return 'text'
      }
      return this.inputType
    },
    styles() {
      return {
        'v--has-error': this.showErrorMessage,
        'v--has-loader': this.loading,
        'v--rounded': this.rounded,
        'v--has-btn-control': this.passwordControl || this.helperControl
      }
    },
    sizeStyles() {
      return {
        [`v--${this.size}`]: true
      }
    }
  },
  methods: {
    blur() {
      if (this.hasError) {
        this.isErrorChecked = true
      } else {
        this.isErrorChecked = false
      }
      this.$emit('blur')
    },
    focus() {
      if (this.$refs.input) {
        this.$refs.input.focus()
      }
    },
    change(e) {
      this.$emit('change', e)
    }
  }
}
</script>

<style lang="stylus" src="./style.styl" scoped></style>
