<template>
  <div>
    <Topbar />
    <form v-if="!done" id="feedbacks" @submit.prevent="slackIt">
      <h2>E aí? O que achou do jogo?</h2>
      <p>Conta pra a gente como foi sua experiência com "O infiltrado"</p>
      <div style="margin-top: 24px;">
        <Field label="Qual o seu nome?" name="name" required v-model="name" />
        <Field
          label="Quer deixar seu e-mail?"
          type="email"
          name="email"
          v-model="email"
        />
        <TextField
          label="Pode ficar a vontade"
          name="name"
          v-model="feedback"
        />
        <Btn style="margin-top: 16px;" class="v--block v--success">
          Enviar
        </Btn>
      </div>
    </form>
    <div v-else>
      <lottie-animation
        style="height: 200px; margin: 60px auto;"
        path="./animations/ok.json"
      />
    </div>
  </div>
</template>
<script>
import axios from 'axios'

export default {
  name: 'feedbacks',
  data() {
    return {
      done: false,
      name: '',
      email: '',
      feedback: ''
    }
  },
  methods: {
    slackIt() {
      axios
        .post(
          'https://hooks.slack.com/services/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',
          {
            username: this.name || 'Detetive Anônimo',
            text: `E-mail: ${this.email}\n\nFeedback: ${this.feedback}`
          },
          {
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
          }
        )
        .then(() => {
          this.done = true
        })
        .catch(() => {
          this.$toast.error('Houve um erro. Por favor, tente novamente.')
        })
    }
  }
}
</script>

<style lang="stylus" scoped>
#feedbacks
  font-family Avenir, Helvetica, Arial, sans-serif
  -webkit-font-smoothing antialiased
  -moz-osx-font-smoothing grayscale
  width 100%
  max-width 1200px
  padding 10px
  margin 40px auto
  font-size 20px
  color #96a5b5
  h3
    margin 20px 0px
  & >>> .c-field_label > label
    color #96a5b5!important
</style>
