<template>
  <div class="c-logoutBtn">
    <Btn @click="openModal" class="c-scoreBoard__vote">Sair</Btn>
    <portal to="modal">
      <LogoutModal
        v-if="modal"
        @close="closeModal"
        :room="room"
        :room-id="roomId"
        :win-on-spy-fall="winOnSpyFall"
      />
    </portal>
  </div>
</template>

<script>
import LogoutModal from '@/components/modals/LogoutModal'
export default {
  name: 'LogoutBtn',
  props: {
    room: {
      type: Object,
      default: () => ({})
    },
    roomId: String,
    winOnSpyFall: {
      type: Boolean,
      default: true
    }
  },
  components: {
    LogoutModal
  },
  data() {
    return {
      modal: false
    }
  },
  methods: {
    openModal() {
      this.modal = true
    },
    closeModal() {
      this.modal = false
    }
  }
}
</script>

