import Vue from 'vue'
import { firestorePlugin } from 'vuefire'
import PortalVue from 'portal-vue'
import LottieAnimation from 'lottie-vuejs'
import Clipboard from 'v-clipboard'
import VueToast from 'vue-toast-notification'
import VueWait from 'vue-wait'
import { VTooltip, VPopover, VClosePopover } from 'v-tooltip'

import '@/assets/css/toast.css'

import App from './App.vue'
import router from './router'
import store from './store'

import db from './plugins/db'

import './components/global'

Vue.config.productionTip = false
Vue.use(LottieAnimation)

Vue.use(db)
Vue.use(PortalVue)
Vue.use(Clipboard)
Vue.use(VueWait)

Vue.use(VueToast)

Vue.use(firestorePlugin)

VTooltip.options = {
  ...VTooltip.options,
  defaultClass: 'c-tooltip',
  defaultTemplate:
    '<div class="c-tooltip" role="tooltip"><div class="c-tooltip__arrow"></div><div class="c-tooltip__inner"></div></div>',
  defaultArrowSelector: '.c-tooltip__arrow',
  defaultInnerSelector: '.c-tooltip__inner',
  defaultDelay: {
    show: 400
  },
  disposeTimeout: 100,
  popover: {
    defaultPlacement: 'bottom',
    defaultClass: 'vue-popover-theme',
    defaultBaseClass: 'c-tooltip c-popover',
    defaultWrapperClass: 'wrapper',
    defaultInnerClass: 'c-tooltip__inner c-popover__inner',
    defaultArrowClass: 'c-tooltip__arrow c-popover__arrow',
    defaultOpenClass: 'open',
    defaultDelay: 0,
    defaultTrigger: 'click',
    defaultOffset: 8,
    defaultContainer: 'body',
    defaultBoundariesElement: undefined,
    defaultPopperOptions: {
      modifiers: {
        arrow: {
          enabled: true
        }
      }
    },
    defaultAutoHide: true,
    defaultHandleResize: true
  }
}

Vue.directive('tooltip', VTooltip)
Vue.directive('close-popover', VClosePopover)
Vue.component('v-popover', VPopover)

new Vue({
  router,
  store,
  wait: new VueWait(),
  toast: VueToast,
  render: (h) => h(App)
}).$mount('#app')
