<template>
  <Modal :controllers="false" @close="$emit('close')">
    <div slot="header" class="v--flex">
      <Btn
        class="c-settingsModal__title"
        :active="option === 1"
        @click="setOption(1)"
        >Banir</Btn
      >
      <Btn
        class="c-settingsModal__title"
        :active="option === 2"
        @click="setOption(2)"
        >Configurar Rounds</Btn
      >
    </div>
    <div v-if="option === 2">
      <Field
        flex
        name="roundLimit"
        inputType="number"
        label="Nº de rodadas"
        v-model="roundLimit"
        :disabled="loading"
      />
      <Field
        flex
        name="roundTime"
        inputType="number"
        label="Duração de cada rodada"
        v-model="roundTime"
        :disabled="loading"
      />
    </div>
    <div class="c-settingsModal__users" v-if="option === 1">
      <template v-for="user in room.users">
        <div v-if="user" :key="user.id" class="c-settingsModal__item">
          <div v-if="user.id !== userId" class="c-settingsModal__person">
            <h3 class="c-settingsModal__name">
              {{ user.name }}
            </h3>
            <Btn
              :disabled="$wait.is('banning')"
              @click="ban(user)"
              class="c-scoreBoard__vote v--small"
            >
              Banir
            </Btn>
          </div>
          <div v-else class="c-settingsModal__person v--info">
            <h3 class="c-settingsModal__name">Tu: {{ user.name }}</h3>
          </div>
        </div>
      </template>
    </div>
    <template v-if="option === 2" #footer>
      <Btn
        class="v--success v--round v--100 v--large"
        @click="updateRoom"
        :disabled="loading"
      >
        Salvar
      </Btn>
    </template>
  </Modal>
</template>
<script>
import { mapState } from 'vuex'
export default {
  name: 'SettingsModal',
  props: {
    room: {
      type: Object,
      required: true
    }
  },
  computed: {
    ...mapState({
      userId: (state) => state.user.id,
      roomId: (state) => state.room.id
    })
  },
  data() {
    return {
      roundLimit: this.room.roundLimit,
      roundTime: this.room.roundTime,
      option: 1,
      loading: false
    }
  },
  methods: {
    updateRoom() {
      this.loading = true
      this.$store
        .dispatch('room/updateRoom', {
          id: this.roomId,
          object: {
            roundLimit: parseInt(this.roundLimit),
            roundTime: parseInt(this.roundTime)
          }
        })
        .then(() => {
          this.$toast.success('Configuração salva!')
        })
        .finally(() => {
          this.loading = false
        })
    },
    setOption(v) {
      this.option = v
    },
    ban(user) {
      this.$wait.start('banning')
      this.$store
        .dispatch('room/banUser', user.id)
        .then(() => {
          this.$toast.success(`${user.name} foi banido!`)
        })
        .catch(() => {
          this.$toast.error('Houve um erro! Tenta de novo')
        })
        .finally(() => {
          this.$wait.end('banning')
        })
    }
  }
}
</script>
